import axios from 'axios'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8'

var timeout = function (flag, url, msg, confirm) {
  if (flag === '-1') {
    confirm(msg, {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      window.parent.location.href = url + '/cmli-vwms'
    }).catch(() => {})
  } else {
    console.log('登陆未超时')
  }
}

var ajax = function (url, data, res, err) {
  axios.post(url, data, { headers: { 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' } }).then(res, err)
}

export default { timeout, ajax }



