<script>
export default {
  name: 'ProjectOverview',
  data () {
    return {
      echart1: null,
      echart2: null,
      echart3: null,
      normal: true, // 实际工作日状态
      isMore: false, // 查看更多
      tabIndex: 1,
      tabData: [{
        name: '项目详情',
        number: 0
      },{
        name: '预警超期',
        number: 15
      },{
        name: '待开始任务',
        number: 8
      }]
    }
  },
  methods: {
    tabClick (index) {
      this.tabIndex = index;
    },
    requestData (data) {
      this.loading = true;
      // this.$refs.vin.focus();
      this.common.ajax(this.url+"tmsIntransitServlet", data, res=>{
      },err =>{
      });
    },
    initEchart () {
      // 发运和签收图表
      this.echart1 = echarts.init(document.getElementById('echart1'));
      this.echart2 = echarts.init(document.getElementById('echart2'));
      this.echart3 = echarts.init(document.getElementById('echart3'));
      // 大小
      var radius = ['45%', '55%'];
      // 位置
      var center = ['50%', '30%'];
      // 指定图表的配置项和数据
      var option1 = {
        color: ['#1AB394', '#FB6059', '#FBAC2A'],
        tooltip: {trigger: 'item', formatter: "{a} <br/>{b}: {c} ({d}%)"},
        legend: {
          orient: 'vertical',
          x: 'center',
          y: 'bottom',
          data:['正常','预警','超期'],
          formatter: function (name) {
            return name + " " + 12;
          }
        },
        series: [
          {
            name:'数据',
            type:'pie',
            radius: radius,
            center: center,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {show: false}
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data:[
              {value:12, name:'正常'},
              {value:5, name:'预警'},
              {value:2, name:'超期'}
            ]
          }
        ]
      };
      var option2 = {
        color: ['#599CE8', '#C9C9C9', '#AAAAAA', '#898989'],
        tooltip: {trigger: 'item', formatter: "{a} <br/>{b}: {c} ({d}%)"},
        legend: {
          orient: 'vertical',
          x: 'center',
          y: 'bottom',
          data:['待开始','未开始','进行中','已完成'],
          formatter: function (name) {
            return name + " " + 12;
          }
        },
        series: [
          {
            name:'数据',
            type:'pie',
            radius: radius,
            center: center,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {show: false}
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data:[
              {value:12, name:'待开始'},
              {value:5, name:'未开始'},
              {value:2, name:'进行中'},
              {value:2, name:'已完成'}
            ]
          }
        ]
      };
      var option3 = {
        color: ['#C9C9C9', '#599CE8', '#AAAAAA'],
        tooltip: {trigger: 'item', formatter: "{a} <br/>{b}: {c} ({d}%)"},
        legend: {
          orient: 'vertical',
          x: 'center',
          y: 'bottom',
          data:['未开始','审批中','已审批'],
          formatter: function (name) {
            return name + " " + 12;
          }
        },
        series: [
          {
            name:'数据',
            type:'pie',
            radius: radius,
            center: center,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {show: false}
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data:[
              {value:335, name:'未开始'},
              {value:310, name:'审批中'},
              {value:234, name:'已审批'}
            ]
          }
        ]
      };
      this.echart1.setOption(option1);
      this.echart2.setOption(option2);
      this.echart3.setOption(option3);
    }
  },
  mounted () {
    this.initEchart();
    setTimeout(() => {
      this.normal = !this.normal;
    }, 1000);
  },
  computed: {
  }
}
</script>

